.ss-header {
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  margin: 0 0 1.8rem;
  line-height: 1.1; }

.ss-header-upper {
  text-transform: uppercase; }

.ss-header-no-margin {
  margin-top: 0;
  margin-bottom: 0; }

.ss-header-scale-none {
  font-size: 1rem; }

.ss-header-scale-sm {
  font-size: 0.9rem; }

.ss-header-scale-md {
  font-size: 1.25rem; }

.ss-header-scale-lg {
  font-size: 1.5rem; }

.ss-header-scale-xl {
  font-size: 2.25rem; }

.ss-header-scale-xxl {
